import { Card, CardContent, Typography } from '@mui/material';
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { selectPrestamo } from '../../redux/actions/prestamo.actions';
import { uiOpenModal } from '../../redux/actions/ui.actions';
import { NormalButton } from '../buttons/NormalButton';
import { ModalPagar } from '../Modal/ModalPagar';
import { SearchModal } from '../QuickSearch/SearchModal';

export const CardPrestamo = () => {
    const dispatch = useDispatch();
    const { lista_prestamos } = useSelector(state => state.prestamo)
    console.log(lista_prestamos);
    const OpenPagar = (data) => {
        dispatch(selectPrestamo(data))
        dispatch(uiOpenModal());
    }

    
    return (
        <>
            {
                lista_prestamos.length > 0 && lista_prestamos.map(p =>
                (
                    <Card sx={{ mt: 2 }} key={p._id} elevation={3}>
                        <CardContent>
                            <Typography>Cliente: {p.cliente}</Typography>
                            <Typography>Fecha Prestamo: {p.fecha_prestamo.split('T')[0]}</Typography>
                            <Typography>Valor Prestamo: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(p.valor_prestamo)}</Typography>
                            <Typography>Capital Actual: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(p.capital_actual)}</Typography>
                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <Typography>Tasa Interes: {p.tasa_interes}</Typography>
                                <NormalButton fullWidth={false} text="Pagar" onClick={() => OpenPagar(p)} />
                            </div>

                        </CardContent>
                    </Card>
                ))
            }
            <ModalPagar title="Pagar Prestamo" />
        </>
    )
}
