import { Container, Divider, Grid, IconButton, TextField, styled, Typography } from '@mui/material';
import Modal from '@mui/material/Modal';
import { Box } from '@mui/system'
import React from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { theme } from '../../configtheme'
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen'
import { uiCloseModal, uiErrorMessage } from '../../redux/actions/ui.actions'
import { NormalButton } from '../buttons/NormalButton';
import { InputNormalNumber } from '../inputs/InputNormalNumber';
import { QsTitle } from '../QuickSearch/QsTitle'



export const ModalPagar = ({ title }) => {
    const isMobile = useCheckMobileScreen();
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        // height: '70vh',
        transform: 'translate(-50%, -50%)',
        width: `${isMobile ? '80%' : '60%'}`,
        bgcolor: 'background.paper !important',
        border: `1px solid ${theme.palette.valen.tertiary}`,
        borderRadius: 8,
        boxShadow: 24,
        p: 4,
        background: 'white'
    };

    const [valor_pagar, setValor_pagar] = useState('');
    const [fecha_pago, setFecha_pago] = useState('');
    const [valor_interes, setValor_interes] = useState(0);
    const dispatch = useDispatch();
    const { modalOpen } = useSelector(state => state.ui);

    const { prestamo } = useSelector(state => state.prestamo);

    useEffect(() => {
        calcularInteres();
        return () => {
        }
    }, [prestamo])


    const calcularInteres = () => {

    }

    const handleChange = (e) => {
        setValor_pagar(e.target.value);
    }

    const handleChangeFechaPago = (e)=>{
        setFecha_pago(e.target.value);
    }

    const handleClose = () => {
        dispatch(uiCloseModal());
    }

    const handlePagar = (e) => {
        e.preventDefault();
        if(!valor_pagar) return dispatch(uiErrorMessage("Digite correctamente el valor a pagar"));
        if(!fecha_pago) return dispatch(uiErrorMessage("Digite correctamente la fecha de pago"));
        console.log(valor_pagar);
        console.log(fecha_pago);
    }
    function isEmpty(obj) {
        return Object.keys(obj).length === 0;
    }
    return (
        <div>
            {
                !isEmpty(prestamo) && (
                    <Modal
                        open={modalOpen}
                        onClose={handleClose}
                        aria-labelledby="modal-modal-title"
                        aria-describedby="modal-modal-description"
                    >
                        <Box sx={style}>
                            <QsTitle title={title} handleClose={handleClose} />
                            <Divider sx={{ mt: 2, mb: 2 }} />
                            <Container component="main" maxWidth="lg" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }} >
                                <Grid container spacing={2} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }}>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Cliente: {prestamo.cliente}</Typography>
                                    </Grid>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Fecha prestamo: {prestamo.fecha_prestamo.split('T')[0]}</Typography>
                                    </Grid>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Valor Prestamo: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.valor_prestamo)}</Typography>
                                    </Grid>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Capital Actual: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.capital_actual)}</Typography>
                                    </Grid>

                                    <Grid item xs={12} md={4}>
                                        <Typography title="Este valor sera descontado del valor a pagar luego de procesar el pago"> <b>Valor Interes</b>: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.valor_interes)}</Typography>
                                    </Grid>

                                    <Grid item xs={12} md={4}>
                                        <InputNormalNumber
                                            margin="dense"
                                            id="valor_pagar"
                                            label="Valor a Pagar"
                                            name="valor_pagar"
                                            onChange={handleChange}
                                            value={valor_pagar}
                                            prefix="$"
                                        />
                                    </Grid>

                                    <Grid item xs={12} md={4}>
                                        <TextField
                                            type="date"
                                            margin="dense"
                                            id="fecha_pago"
                                            label="Fecha pago"
                                            name="fecha_pago"
                                            InputLabelProps={{ shrink: true }}
                                            onChange={handleChangeFechaPago}
                                            value={fecha_pago}
                                            prefix="$"
                                        />
                                    </Grid>

                                    <NormalButton text="Pagar" onClick={handlePagar} />


                                </Grid>
                            </Container>


                        </Box>
                    </Modal>
                )
            }

        </div>
    );
}
